import { NgModule } from '@angular/core';
import {RouterModule, Routes} from "@angular/router";

import {ShareModule} from "../../shareModel/share.module";

import { CRMManageService } from './crm-manage.service';
import {CycleButComponent} from "./cycle-but/cycle-but.component";

const CRM_routes = [
  CycleButComponent,
];

const CRM_ROUTES: Routes = [
  {
      path: 'crm_manage/cycle-but/cycle-but',
      component: CycleButComponent
  }
];

@NgModule({
  imports: [
      RouterModule.forChild(CRM_ROUTES),
      ShareModule
  ],
  declarations: [...CRM_routes],
  providers: [CRMManageService],
  exports: [
      ...CRM_routes,
      RouterModule
  ],
})
export class CRMManageModule { }
